/* eslint-disable linebreak-style */
/* eslint-disable react/jsx-indent */

import React from 'react'
import { Switch, Redirect, Route } from 'react-router-dom'
// import Welcome from '../views/Auth/Welcome'
import Login from '../views/Auth/Login/index'
import SignUp from '../views/Auth/SignUp/index'
import EnterOtpForm from '../views/Auth/EntetOtp/index'
import ForgotPassword from '../views/Auth/ForgotPassword/index'
import Products from '../views/Products/index'
import ProductDetails from '../views/Products/ProductDetails/index'
import HomeContent from '../views/Home/index'
import ArtistDetailsContent from '../views/ArtistDetails/index'
import MywishListContent from '../views/MywishList/index'
import MycartContent from '../views/MyCart/index'
import CheckoutContent from '../views/Checkout/index'
import MyOrdersContent from '../views/MyOrders/index'
import MoreDetailsContent from '../views/MyOrders/MoreDetails/index'
import PersonalInfo from '../views/MyAccount/User/personal-info'
import ChangePwd from '../views/MyAccount/User/change-pwd'
import AddressList from '../views/MyAccount/User/address-list'
import Reviews from '../views/MyAccount/User/reviews'
import Notification from '../views/MyAccount/User/notification'
import Token from '../views/MyAccount/User/token'
/* Artist Panel */
import ArtistProfileContent from '../views/Artist Panel/ArtistProfile/index'
import DashboardContent from '../views/Artist Panel/Dashboard/index'
import NewProjectsContent from '../views/Artist Panel/NewProjects/index'
import MyProjectDetailsContent from '../views/Artist Panel/MyProjectDetails/index'
import ArtistPersonalInformation from '../views/MyAccount/Artist/personal-information'
import BusinessDetails from '../views/MyAccount/Artist/business-details'
import ArtistChangePwd from '../views/MyAccount/Artist/change-pwd'
import NotificationsContent from '../views/Artist Panel/Notifications/index'
import PricingContent from '../views/Artist Panel/Pricing/index'
import MyProjectsContent from '../views/Artist Panel/MyProjects/index'
import ReviewsContent from '../views/Artist Panel/Reviews/index'
import PaymentsContent from '../views/Artist Panel/Payments/index'
import OrdersContent from '../views/Artist Panel/Orders/index'
import OrderDetailsContent from '../views/Artist Panel/Orders/OrderDetails/index'
import ProjectsContent from '../views/Artist Panel/Projects/index'
import ChatContent from '../views/Artist Panel/Chat/index'
import CustomizeDesignContent from '../views/Artist Panel/Customize/index'

// B2B
import B2BSignup from '../views/B2B/SignUp'
import B2BLogin from '../views/B2B/Login'
import B2Bforgot from '../views/B2B/ForgotPassword'
import B2BenterOtp from '../views/B2B/EnterOtp'
import B2Bhome from '../views/B2B/Home'
import B2Bproducts from '../views/B2B/Products'
import B2BproductDetails from '../views/B2B/Products/ProductDetails'
import B2BartistDetails from '../views/B2B/ArtistDetails'
import B2Bwishlist from '../views/B2B/MywishList'
import B2Bcart from '../views/B2B/MyCart'
import B2Bcheckout from '../views/B2B/Checkout'
import B2BmyOrders from '../views/B2B/MyOrders'
import B2BmyOrderDetails from '../views/B2B/MyOrders/MoreDetails'
import B2BprofileSetup from '../views/B2B/ProfileSetup'

import PublicRoutes from './PublicRoutes'
// import PrivateRoutes from './PrivateRoutes'

const Routes = () => (
<Switch>
  <PublicRoutes path={`${process.env.PUBLIC_URL}/login`} component={Login} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/enter-otp`} component={EnterOtpForm} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/sign-up`} component={SignUp} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/forgot-pwd`} component={ForgotPassword} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/home`} component={HomeContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/products`} component={Products} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/product-details`} component={ProductDetails} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/artist-details`} component={ArtistDetailsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/my-wishlist`} component={MywishListContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/my-cart`} component={MycartContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/checkout`} component={CheckoutContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/my-orders`} component={MyOrdersContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/more-details`} component={MoreDetailsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/personal-information`} component={PersonalInfo} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/change-pwd`} component={ChangePwd} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/address`} component={AddressList} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/myaccount-reviews`} component={Reviews} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/user-notification`} component={Notification} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/token`} component={Token} exact />
  {/* Artist Panel */}
  <PublicRoutes path={`${process.env.PUBLIC_URL}/artist-profile-setup`} component={ArtistProfileContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/dashboard`} component={DashboardContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/new-projects`} component={NewProjectsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/my-project-details`} component={MyProjectDetailsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/artist-personal-infomation`} component={ArtistPersonalInformation} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/business-details`} component={BusinessDetails} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/artist-change-pwd`} component={ArtistChangePwd} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/notifications`} component={NotificationsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/pricing`} component={PricingContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/my-projects`} component={MyProjectsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/reviews`} component={ReviewsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/payments`} component={PaymentsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/orders`} component={OrdersContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/order-details`} component={OrderDetailsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/projects`} component={ProjectsContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/chat`} component={ChatContent} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/customize-design`} component={CustomizeDesignContent} exact />
   {/* B2B */}
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-sign-up`} component={B2BSignup} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-login`} component={B2BLogin} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-forgot`} component={B2Bforgot} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-enter-otp`} component={B2BenterOtp} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-home`} component={B2Bhome} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-products`} component={B2Bproducts} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-product-details`} component={B2BproductDetails} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-artist-details`} component={B2BartistDetails} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-my-wishlist`} component={B2Bwishlist} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-my-cart`} component={B2Bcart} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-checkout`} component={B2Bcheckout} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-my-orders`} component={B2BmyOrders} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-more-details`} component={B2BmyOrderDetails} exact />
   <PublicRoutes path={`${process.env.PUBLIC_URL}/b2b-profile-setup`} component={B2BprofileSetup} exact />
   
   <Redirect path={`${process.env.PUBLIC_URL}/`} to={`${process.env.PUBLIC_URL}/login`} />
</Switch>
)

export default Routes
